<template>
  <div id="app">
    <div class="app-container">
      <!-- autocomplete -->
      <app-autocomplete :world-countries="worldCountries" @select="onSelect"></app-autocomplete>
      <!-- results -->
      <template v-if="userData.length > 0">
        <div v-for="(stats, index) of userData" :key="index">
          <app-statistics :statistics="stats" @delete="onDelete"></app-statistics>
        </div>
      </template>
      <template v-else>Start by Adding Countries</template>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import AppAutocomplete from "./components/AppAutocomplete";
import AppStatistics from "./components/AppStatistics";

export default {
  name: "App",

  components: {
    AppAutocomplete,
    AppStatistics
  },

  data() {
    return {
      worldCountries: [],
      userCountries: new Set(),
      userData: []
    };
  },

  methods: {
    onSelect(country) {
      this.userCountries = new Set(
        this.userCountries.add(country.toLowerCase().toString())
      );
      const queryData = JSON.stringify(Array.from(this.userCountries));
      const query = `
      query statsQuery {
          stats(countries: ${queryData}) {
            country
            cases
            deaths
            active
            critical
            recovered
            todayCases
            todayDeaths
            casesPerOneMillion
          }
        } 
      `;
      axios({
        url: "/graphql",
        method: "post",
        data: { query: query }
      }).then(result => {
        const { data } = result.data;
        this.userData = data.stats;
      });
    },
    onDelete(country) {
      if (this.userCountries.delete(country.toLowerCase())) {
        this.userCountries = new Set(this.userCountries);
        this.userData = this.userData.filter(item => {
          return item.country.toLowerCase() != country.toLowerCase();
        });
      }
    }
  },

  created() {
    const query = `
      query countryQuery {
          countries {
            country
          }
        } 
      `;
    axios({
      url: "/graphql",
      method: "post",
      data: { query: query }
    }).then(result => {
      const { data } = result.data;
      this.worldCountries = data.countries.map(item => {
        return item.country.toLowerCase();
      });
    });
  }
};
</script>

