<template>
  <div id="app-statistics">
    <template v-if="statistics">
      <div class="header">
        <div>{{ statistics.country }}</div>
        <button class="btn" @click.stop="$emit('delete', statistics.country)"><font-awesome-icon icon="times" /></button>
      </div>
      <div class="summary">
        <div class="stats-item">
          <div class="stat-item-number">{{ statistics.cases }}</div>
          <small class="stat-item-category">Total Cases</small>
          <div>
            <small>Today: {{ statistics.todayCases }}</small>
          </div>
        </div>
        <div class="stats-item">
          <div class="stat-item-number">{{ statistics.deaths }}</div>
          <small class="stat-item-category">Total Deaths</small>
          <div>
            <small>Today: {{ statistics.todayDeaths }}</small>
          </div>
        </div>
      </div>
      <div class="info">
        <div>
          <div class="info-number">{{ statistics.active }}</div>
          <small>active</small>
        </div>
        <div>
          <div class="info-number">{{ statistics.critical }}</div>
          <small>critical</small>
        </div>
        <div>
          <div class="info-number">{{ statistics.recovered }}</div>
          <small>recovered</small>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  name: "app-statistics",
  props: {
    statistics: {
      type: Object,
      default() {
        return [];
      }
    }
  }
};
</script>

<style lang="scss">
#app-statistics {
  padding: 1rem;
  background: rgb(173, 14, 93);
  border-radius: 12px;
  margin-bottom: 0.5rem;
  color: #fff;

  .header {
    margin-bottom: 1em;
    font-weight: bold;
    font-size: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .summary {
    display: grid;
    grid-column-gap: 0.25rem;
    grid-template-columns: 0.5fr 0.5fr;
    margin-bottom: 1rem;

    .stats-item {
      text-align: center;
      background: lighten(rgb(173, 14, 93), 5);
      padding: 1rem 0;

      .stat-item-number {
        font-size: 24px;
        font-weight: 100;
      }
      // margin: 0 0.25rem;
    }
  }

  .info {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    text-align: center;
    font-size: 12px;
    color: lighten(rgb(173, 14, 93), 50);
    margin-bottom: 1rem;
  }

  .btn {
    background: rgb(173, 14, 93);
    border: none;
    color: #fff;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    &:hover {
      background: lighten(rgb(173, 14, 93), 5);
    }
  }
}
</style>
