<template>
  <div id="app-autocomplete">
    <input id="app-autocomplete-input" type="text" autocomplete="off" placeholder="Country Name..." v-model="userCountry" @input="onInput" />
    <ul id="app-autocomplete-menu" v-show="isOpen">
      <li class="app-autocomplete-menu-item" v-for="(country, index) of menuCountries" :key="index" @click.stop="onSelect(country)">{{ country }}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "app-autocomplete",
  props: {
    worldCountries: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  data() {
    return {
      userCountry: "",
      menuCountries: []
    };
  },
  computed: {
    isOpen() {
      return this.userCountry ? true : false;
    }
  },
  methods: {
    onInput() {
      this.menuCountries = this.worldCountries.filter(country => country.startsWith(this.userCountry));
    },
    onSelect(country) {
      this.userCountry = "";
      this.$emit("select", country);
    }
  }
};
</script>

<style lang="scss">
#app-autocomplete {
  position: relative;

  #app-autocomplete-input,
  #app-autocomplete-menu {
    width: 100%;
  }

  #app-autocomplete-input {
    height: 36px;
    padding: 0 0.25rem;
    border: none;
    border-radius: 4px;
    margin-bottom: 0.5rem;
    top: 0;
    left: 0;

    &:focus {
      outline: none;
    }
  }

  #app-autocomplete-menu {
    position: absolute;
    background: #fff;
    list-style: none;
    padding: 0;
    margin: 0;
    max-height: 200px;
    overflow-y: scroll;
    top: 40px;

    .app-autocomplete-menu-item {
      padding: 1rem 0.25rem;
      cursor: pointer;

      &:hover {
        background-color: lighten($color: #000, $amount: 98);
      }
    }
  }
}
</style>
